@import url(https://fonts.googleapis.com/css2?family=Sail&display=swap);
/* base styles */
body {
  margin: 0;
  display: flex;
  justify-content: center;
  background: #fac45a;
  color: #990629;
  font-family: 'Sail', cursive;
  background-image: url(/static/media/background.6b03f7eb.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

}
.App {
  max-width: 916px;
  margin: 40px auto;
  text-align: center;
}

.new-button {
  width:200px;
  height:100px;
  cursor: pointer;
  background-image: url(/static/media/scroll.93155b99.png);
  font-family: fantasy;
  color: black;
  background-position: center;
  background-size: 200px 100px;
  background-repeat: no-repeat;
  background-color: transparent;
  border:none;
  font-size: 1.5em;
}


.card-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap:20px;
  width: 100%;
  margin: 15px 0 30px 0;
}

h1 {
  font-size:4em;
}

p {
  font-family: fantasy;
  color: black;
}

.card-container {
    width:150px;
    height: 150px;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .card {
    transition: all ease-in 0.2s;
    position: absolute;
    height: 95%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    border: 3px solid black;

  }

  .card img {
    width:95%;
    display:block;

  }
  
  .card:hover {
    cursor:pointer;
  }
  
  .card-front {
    background-color:  #990629 ;
    transform: rotateY(90deg);
  }

  .flipped .card-front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
  }

.card-back {
    background-color: burlywood;
    transition-delay: 0.2s;
  }


  .flipped .card-back {
    transform: rotateY(90deg);
    transition-delay: 0s;
  }

